<template>
  <div>
    <h3 style="position auto">
      欢迎你
      {{this.$store.state.nickname}}
    </h3>
    <div
      class="demo-image__placeholder">
      <div class="block">
        <span
          class="demonstration"></span>
        <el-image :src="src"
          style="width: 20em,; height: 15em"
          :fit="this.fit">
        </el-image>
      </div>

    </div>
    <p>
      {{data.sentence}}
    </p>
    <p
      style="position:absolute;right: 1vh">
      ------{{data.author}}
    </p>

    <el-button round id="btn1"
      @click="startAdd">开始录入单词
    </el-button>
    <el-button type="primary"
      @click="startRecite"
      round id="btn2">开始背今日单词
    </el-button>

  </div>
</template>

<script>
import axios from 'axios'

export default {
  components: {},
  methods: {
    startRecite() {
      this.$router.push('/reciteWords')
    },
    startAdd() {
      this.$router.push('/addWords')
    },
  },
  name: 'MyIndex',
  data() {
    return {
      data: '',
      src: '',
      fit:'contain',
    }
  },
  mounted() {
    if (
      this.$store.state.uid == null ||
      this.$store.state.uid == '' ||
      this.$store.state.uid===undefined
    ) {
   
      this.$router.push('/login')
    }
  

    axios({
      method: 'post',
      url: 'http://121.40.113.226:7778/getOne',
      data: {},
    }).then((res) => {
      this.data = res.data.data
      this.src = this.data.pic
    })
  },
}
</script>
<style>
#btn1 {
  position: absolute;
  left: 1vh;
  bottom: 10vh;
}
#btn2 {
  position: absolute;
  right: 1vh;
  bottom: 10vh;
}
</style>